table{
  border-radius:4px;
  font-family: sans-serif, Calibri, Gill Sans, Gill Sans MT,  Trebuchet MS;
  background-color: white;
}
table, th, td {
    border-collapse: collapse;
    border-bottom: 2px solid #E6ECF0;
    text-align:center;
    font-size: 12px;
}
table th{
  font-weight: 600;
  background-color: #F4F4F4;
  border-top:4px solid #E6ECF0;
  padding: 10px;
}
th ,td{
    width:fit-content;
    min-height:20px;
}

.input-field{
  width:40px;
  height:28px;
  border:1px solid #D9D9D9 !important;
  text-align: center;
  outline:none;
}

.input-field:focus,

.input-field:active{
  border:1px solid rgb(85, 89, 91) !important;
}
